<template>
  <div class="LoginRegister">
    <v-overlay :value="isLoading">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <v-container>
      <v-form>
        <v-row>
          <v-flex class="xs12 lg6 pa-2">
            <v-text-field v-model="email" label="E-Mail"></v-text-field>
          </v-flex>
          <v-flex class="xs12 lg6 pa-2">
            <v-text-field v-model="password" label="Contraseña"></v-text-field>
          </v-flex>
          <v-flex class="xs12 lg6 pa-2">
            <v-select v-model="userType" label="Tipo de Usuario" :items="items"></v-select>
          </v-flex>
        </v-row>
        <v-row>
          <v-flex class="xs12 lg6 pa-2">
            <v-btn class="full-width primary" tile @click="signUp()">Registrarse</v-btn>
          </v-flex>
          <v-flex class="xs12 lg6 pa-2">
            <v-btn class="full-width primary" tile @click="signIn()">Ingresar</v-btn>
          </v-flex>
        </v-row>
      </v-form>
    </v-container>
  </div>
</template>

<script>
import { firebase, firestore } from "../firebase";
export default {
  name: "LoginRegister",
  data: function() {
    return {
      isLoading: false,
      email: "",
      password: "",
      userType: "Profesor",
      items: ["Profesor", "Alumno"]
    };
  },
  methods: {
    signUp() {
      this.isLoading = true;
      const newUser = {
        email: this.email,
        password: this.password,
        userType: this.userType
      };
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(() => {
          firebase
            .firestore()
            .collection("users")
            .add(newUser)
            .then(() => {
              this.isLoading = false;
            });
        });
    },
    signIn() {
      this.isLoading = true;
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(() => {
          this.isLoading = false;
          this.$router.push("/");
        })
        .catch(err => {
          this.isLoading = false;
        });
    }
  }
};
</script>

